// Dashboard Component

import React from 'react'
import Helmet from 'react-helmet'
import PropTypes from 'prop-types'
import Content from '../Content'
import GoogleMap from '../GoogleMap'

import handleClick from '../../handleClick.js'
import routeChange from '../../routeChange.js'

import useAuth from '../useAuth'
import { Amplify, Auth } from 'aws-amplify';
import awsconfig from '../../aws-exports';
Amplify.configure(awsconfig);

import CarInterface from './index.tsx'


const DashboardPageTemplate = ({ meta_title, meta_description, title, team, content, contentComponent }) => {
  const PageContent = contentComponent || Content

  const { currentUser } = useAuth();

  return (
    <div className='flex flex-col bg-indigo-100'>
      <div className='container px-8 mx-auto'>
        <Helmet>
          <title>{meta_title}</title>
          <meta name='description' content={meta_description} />
        </Helmet>
          
          <CarInterface />
          
        <main className='flex flex-wrap items-center py-12 my-12 text-center text-gray-800'>
          <div className='w-full whitespace-pre-line md:w-full'>
            <h1 className='mb-6 text-5xl leading-tight'>
              {title}
            </h1>
          
          {(currentUser  !== null) ?
                (<div>
                      <div> Blocktag dashboard work in progress. Please email hello@blocktag.com if you have questions or click button below to download tags for printing!
                      </div>
             
                     <button onClick={() => {
                         handleClick("DownloadTagNavBar");
                         routeChange('https://generate.blocktag.com');
                         window.lintrk('DownloadTagNavBar', { conversion_id: 12784580 });  // LinkedIn Ads
                     }} class="flex-grow inline-block px-8 py-2 my-4 mr-5 text-sm text-white bg-teal-500 border border-teal-500 rounded sm:my-0 hover:bg-teal-700 hover:border-teal-700">Test Print Tags</button>
                 
                </div>)
              : null
          }
          

            <PageContent className='mb-6 markdown' content={content} />
          </div>
        </main>

        {/* Google Maps */}
        <h2 className='m-12 text-4xl leading-tight text-center text-gray-800'></h2>

        <div className='flex flex-wrap justify-center'>
                    
          <GoogleMap latitude={"0.0"} longitude={"0.0"}/>

          
          

        </div>

      </div>
    </div>
  )
}

DashboardPageTemplate.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string,
  contentComponent: PropTypes.func,
}

export default DashboardPageTemplate
